@import "variables";
@import "mixins";
.excel_table {
  position: absolute;
  left: 0;
  right: 0;
  top: $header-height + $formula-height + $toolbar-height   ;
  overflow-x: auto;
  padding-bottom: 2px;
  .row {
    display: flex;
    flex-direction: row;
    height: $row-height;
    min-height: 20px;


  }
  .row-info {
    display: flex;
    justify-content: center;
    min-width: $info-cell-with;
    align-items: center;
    height: 100%;
    border: 1px solid $border-color;
    background: #f8f9fa;
    border-top: none;

  }
  .row-data {
    display: flex;

  }
  .column {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f8f9fa;
    min-width: 40px;
    width: $cell-with;
    border: 1px solid $border-color;
    height: 100%;
    border-top: none;
    border-left: none;
  }
  .cell {
    min-width:40px  ;
    padding: 5px;
    width: $cell-with;
    height: 100%;
    border: 1px solid #e2e3e3 ;
    border-top: none;
    border-left: none;
    color: #111;
    white-space: nowrap;
    outline: 0;

    &.selected {
      border: none;
      outline: 2px solid #3c74ff ;
      z-index: 2;



    }
  }
}